<template>
  <div>
    <h1 class="title"></h1>
    <div class="container">
      <div class="upload-section">
        <div class="description">
          : PDF100
        </div>
        <el-form ref="uploadForm" enctype="multipart/form-data" class="form-container">
          <el-upload
            ref="upload"
            action="/ss/service/uploadFileOperation"
            :auto-upload="false"
            :on-change="handleFileChange"
            :file-list="fileList"
            :on-remove="handleRemove"
            :accept="'.jpg,.jpeg,.png,.pdf'"
            multiple>
            <el-button slot="trigger" type="primary" :disabled="isUploading"></el-button>
            <el-button type="primary" @click="submitUpload" :disabled="isUploading"></el-button>
            <el-button type="primary" @click="clearFileSelection" :disabled="isUploading"></el-button>
            <el-button type="primary" @click="goToPreview" :disabled="isUploading"></el-button>
          </el-upload>
          <div class="file-count">
            : {{ selectedFileCount }} / 100
          </div>
        </el-form>
      </div>
      <div class="log-section">
        <h2></h2>
        <div class="block">
          <div class="radio">
            
            <el-radio-group v-model="reverse">
              <el-radio :label="true"></el-radio>
              <el-radio :label="false"></el-radio>
            </el-radio-group>
          </div>
          <div style="height: 20px;"></div>
          <div class="timeline-container">
            <el-timeline :reverse="reverse">
              <el-timeline-item
                v-for="(activity, index) in activities"
                :key="index"
                :timestamp="activity.timestamp">
                {{ activity.content }}
              </el-timeline-item>
            </el-timeline>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import { Message } from 'element-ui';
import SensorFileRequest from "../../utils/sensorFile";
export default {
    data() {
      return {
        fileList: [], // 
        loadingMessage: null, // 
        reverse: false,
        activities: [], // 
        isLoading: true, // 
        selectedFileCount: 0 ,
        isUploading: false // 
      };
    },
    methods: {
      handleFileChange(file, fileList) {
        this.fileList = fileList;
        this.selectedFileCount = fileList.length; // 
      },
      handleRemove(file, fileList) {
        this.fileList = fileList;
        this.selectedFileCount = fileList.length; // 
      },
      submitUpload() {
        if (this.fileList.length === 0) {
          Message.warning('');
          return;
        }
        // 
        const maxFiles = 100;
        if (this.fileList.length > maxFiles) {
          this.showMessage(`${this.fileList.length}${maxFiles}, `, 'error',3000);
          setTimeout(() => {
            this.clearFileSelection(); // 
          }, 1500); // 1.5
          return;
        }
        // 
        const fileNames = this.fileList.map(file => file.name);
        const uniqueFileNames = new Set(fileNames);
        if (fileNames.length !== uniqueFileNames.size) {
          this.showMessage(' ', 'error',3000);
          setTimeout(() => {
            this.clearFileSelection(); // 
          }, 2000); // 1.5
          return;
        }

        //  true
        this.isUploading = true;

        const formData = new FormData();
        this.fileList.forEach(file => {
          formData.append('multipartFiles', file.raw);
        });
        this.loadingMessage = this.showLoadingMessage('...');
        axios.post('/ss/service/uploadFileOperation', formData)
          .then((response) => {
            const data = response.data;
            if (data.result === 1) {
              this.showMessage(response.data.msg, 'success', 10000); // 10
              this.fetchActivities(); // 
            }
          })
          .catch((error) => {
            console.error(':', error);
            this.showMessage(error, 'error', 10000); // 10
          })
          .finally(() => {
            if (this.loadingMessage) {
              this.loadingMessage.close(); // 
              //  false
              this.isUploading = false;
            }
            setTimeout(() => {
              this.clearFileSelection(); // 
            }, 5000); // 3
          });

      },
      clearFileSelection() {
        this.fileList = [];
        this.selectedFileCount = 0; // 
        this.$refs.upload.clearFiles();
        this.showMessage('', 'success',2000);
      },
      showLoadingMessage(message) {
        return Message({
          message: message,
          type: 'info',
          duration: 0, // 
          showClose: true,
          customClass: 'loading-message'
        });
      },
      showMessage(message, type, duration) {
        Message({
          message: message,
          type: type,
          duration: duration, // 2
          showClose: true
        });
      },
      goToPreview() {
        this.$router.push({ path: '/ssPrice/web/viewFile' });
      },
      fetchActivities() {
        SensorFileRequest.uploadFileLog( response => {
            console.log('Received response data:', response); // 
            if (Array.isArray(response) && response.length > 0) {
              this.activities = response.map(item => ({
                content: item.content,
                timestamp: item.timestamp
              })).reverse();
            } else {
              this.activities = []; // 
              this.$message({
                message: ' ',
                type: 'info',
                duration: 2000, // 2
                showClose: true // 
              });
            }
            this.isLoading = false; //  isLoading  false
          },
          (error) => {
            console.error(error);
            this.$message({
              message: error,
              type: 'error'
            });
            this.isLoading = false;
          });
      }
    },
    mounted() {
      this.fetchActivities(); // 
    }
  }
</script>


<style scoped>
.file-count {
  margin-top: 20px;
  font-size: 16px;
  color: #606266;
}

.title {
  margin-top: 30px;
  text-align: center;
  margin-bottom: 40px;
}
.container {
  display: grid;
  grid-template-columns: 3fr 2fr; /*  */
  gap: 20px;
  padding: 20px;
}
.upload-section {
  background-color: #f9f9f9;
  padding: 30px; /*  */
  border-radius: 12px; /*  */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /*  */
  margin-bottom: 20px; /*  */
}
.log-section {
  background-color: #f9f9f9;
  padding: 30px; /*  */
  border-radius: 12px; /*  */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /*  */
  margin-bottom: 20px; /*  */
}
.description {
  display: block;
  font-size: 15px;
  color: #888;
  margin-bottom: 40px;
}
.no-logs {
  text-align: center;
  color: #888;
}
.log-list {
  list-style-type: none;
  padding: 0;
}
.log-item {
  padding: 8px 0;
  border-bottom: 1px solid #ddd;
}
.log-item:last-child {
  border-bottom: none;
}
.welcome-container {
  flex-direction: column;
  min-height: 95vh;
  padding: 1rem;
}
.header {
  display: block;
  font-size: 2.5em;
  font-weight: bold;
  margin-bottom: 1rem;
  text-align: center;
  color: #212529;
  overflow-wrap: break-word;
  max-width: 340px;
}
.timeline-container {
  max-height: 600px; /*  */
  overflow-y: auto; /*  */
  //border: 1px solid #c7c9cb; /*  */
  padding: 15px 5px; /* 10px5px */
  margin: 0 15px; /*  */
}
.footer {
  margin-top: 20px;
  font-size: 15px;
  color: #666;
}
</style>



